<template>
  <b-modal
    id="modal-add-product"
    hide-header-close
    no-close-on-backdrop
    no-fade
    size="md"
    ref="modalDetail"
    ignore-enforce-focus-selector="span.select2-container"
    title="Thêm dịch vụ"
  >
    <div class="row">
      <div class="col-12">
        <div class="form-group">
          <label for="">Tên dịch vụ</label>
          <input
            type="text"
            v-model="Product.product_name"
            class="form-control"
            placeholder="Tên dịch vụ"
          />
        </div>

        <div class="form-group">
          <label for="">Giá dịch vụ</label>
          <money
            class="form-control"
            v-bind="money"
            v-model="Product.product_price"
          />
        </div>

        <div class="form-group">
          <label for="">Giá thành phần</label>
          <input
            type="text"
            v-model="Product.product_component_price"
            class="form-control"
            placeholder="Giá thành phần"
          />
        </div>

        <div class="form-group">
          <label for="">Nhóm dịch vụ</label>
          <select v-model="Product.cate_id" class="form-select">
            <option value="0">--Chọn nhóm dịch vụ--</option>
            <option :value="item.cate_id" v-for="item in categories" :key="item.cate_id">
              {{ item.cate_name }}
            </option>
          </select>
        </div>

        <div class="form-group mt-2">
          <div class="form-check">
            <input
              class="form-check-input"
              :value="Product.product_split"
              v-model="Product.product_split"
              type="checkbox"
              id="flexCheckDefault"
            />
            <label class="form-check-label" for="flexCheckDefault"> Phân rã </label>
          </div>
        </div>
      </div>
    </div>
    <template #modal-footer>
      <button @click="saveProduct" class="btn btn-primary-soft">
        <i class="fa-thin fa-floppy-disk"></i> Lưu
      </button>
      <button class="btn btn-secondary" @click="$bvModal.hide('modal-add-product')">
        <i class="fa-thin fa-xmark"></i> Đóng
      </button>
    </template>
  </b-modal>
</template>

<script>
import axios from "axios";
import { Money } from "v-money";
export default {
  components: {
    Money,
  },
  data() {
    return {
      Product: {
        product_name: "",
        cate_id: 0,
        product_component_price: 0,
        product_split: false,
        product_price: 0,
      },
      categories: [],
      isUpdate: false,
     
      money: {
        decimal: ",",
        thousands: ".",
        prefix: "",
        suffix: " ",
        precision: 4,
        masked: false,
      },
    };
  },
  created() {
    this.getCategories();
  },
  methods: {
    async saveProduct() {
      this.$econtract.showLoading();
      if (!this.isUpdate) {
        await axios.post(`/products`, this.Product).then((response) => {
          if (response.status == 200) {
            this.$toast.success("Thêm dịch vụ thành công");
            this.$emit("addProductSuccess", response.data);
            localStorage.removeItem('nhl_products');
            this.hide();
          }
        });
      } else {
        await axios.put(`/products/update`, this.Product).then((response) => {
          if (response.status == 200) {
            this.$toast.success("Cập nhật dịch vụ thành công");
            this.$emit("updateProductSuccess", response.data);
            localStorage.removeItem('nhl_products');
            this.hide();
          }
        });
      }
      this.$econtract.hideLoading();
    },
    hide() {
      this.$bvModal.hide("modal-add-product");
    },
    show(item) {
     
      if (item) {
        this.Product = item;
        delete this.Product.cate_name;
        delete this.Product.cate_icon;
        delete this.Product.cate_list_check;
        delete this.Product.pattern_contract;
        this.isUpdate = true;
      } else {
         this.Product = {}
      }
      this.$bvModal.show("modal-add-product");
    },
    async getCategories() {
      await axios.get(`/categories`).then((response) => {
        this.categories = response.data;
      });
    },
  },
};
</script>
