<template>
<div id="product-list">
    <h1 class="fw-bold title-page">Danh sách dịch vụ</h1>
    <!-- <button class="btn btn-primary-soft mb-2" @click="addProduct">Thêm dịch vụ</button> -->
    <div class="row mb-2">
        <div class="col-12">
            <div class="input-group">
                <input type="search" placeholder="Nhập tên dịch vụ để tìm kiếm...." class="form-control" v-model="keyword" @keyup.enter="search()" />
                <button type="button" id="button-addon2" class="btn btn-primary-soft" @click="search()">
                    <i class="fa-solid fa-magnifying-glass"></i> Tìm kiếm
                </button>
                <button class="btn btn-primary-soft ms-1 " @click="addProduct"> <i class="fa-duotone fa-circle-plus"></i> Thêm dịch vụ</button>
            </div>
        </div>
    </div>
    <product-list @deleteProduct="deleteProduct" @updateProduct="updateProduct" :products="products"></product-list>
    <productAdd ref="productAdd" @updateProductSuccess="updateProductSuccess" @addProductSuccess="addProductSuccess"></productAdd>
</div>
</template>

<script>
import productList from "@/components/products/Index.vue"
import productAdd from "@/extends/ModalAddProduct.vue"
import axios from 'axios'
export default {
    components: {
        productList,
        productAdd
    },
    computed: {
        products() {
            return this.productList.filter((item) => {
                return item.product_name.toLowerCase().indexOf(this.keyword.toLowerCase()) !== -1;
            });
        }
    },
    data() {
        return {
            productList: [],
            keyword: "",
        }
    },
    created() {
        this.getProducts();
    },
    methods: {
        search() {
            this.products = this.products.filter(item => {
                return item.product_name.toLowerCase().indexOf(this.keyword.toLowerCase()) !== -1;
            })
        },
        deleteProduct(item, index) {
            this.$confirm({
                title: "Xóa dịch vụ",
                message: "Bạn có muốn xóa dịch vụ?",
                button: {
                    yes: "Yes",
                    no: "No",
                },
                callback: (confirm) => {
                    if (confirm) {
                        axios.delete(`${API_URL}/products/${item.product_id}`).then((response) => {
                            if (response.status == 200) {
                                this.products.splice(index, 1);
                                this.$toast.success("Xóa dịch vụ thành công");
                            }
                        });
                    }
                },
            });
        },
        updateProductSuccess(item) {
            // this.products.filter(product=>{
            //     return product.item
            // })
        },
        updateProduct(item) {
            this.$refs.productAdd.show(item);
        },
        addProductSuccess(item) {
            this.productList.push(item)
        },
        addProduct() {
            this.$refs.productAdd.show();
        },
        async getProducts() {
            await axios.get(`/products`).then(response => {
                return this.productList = response.data
            })
        }
    }
}
</script>
