<template>
<div>
    <table class="table">
        <thead class="table-dark">
            <tr>
                <th class="rounded-start">#</th>
                <th>Tên dịch vụ</th>
                <th>Nhóm dịch vụ</th>
                <th>Giá dịch vụ</th>
                <th>Giá thành phần</th>
                <th>Phân rã</th>
                <th class="rounded-end"></th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(product,index) in products" :key="product.product_id">
                <td>{{index+1 }}</td>
                <td>
                    <span :title="product.product_name" class="text-primary hand" @click="show(product)">
                        {{ product.product_name }}
                    </span>
                </td>
                <td>{{ product.cate_name }}</td>
                <td>{{ product.product_price  | formatamout(4)}}</td>
                <td>{{ product.product_component_price }}</td>
                <td>
                    <div class="form-check">
                        <input class="form-check-input" @change="updateProduct(product)" type="checkbox" v-model.lazy="product.product_split" :checked="product.product_split" />
                    </div>
                </td>
                <td>
                    <button class="btn btn-primary-soft btn-sm" @click="show(product)">
                        <i class="fa-thin fa-pen-to-square"></i>
                    </button>
                    <button class="btn btn-danger-soft btn-sm ms-1" @click="deleteProduct(product,index)">
                        <i class="fa-thin fa-trash-alt"></i>
                    </button>
                </td>
            </tr>
        </tbody>
    </table>
</div>
</template>

<script>
export default {

    props: {
        products: Array
    },
    methods: {
        async updateProduct(item) {
            let product = item
            await axios.put(`${API_URL}/products/update`, product).then((response) => {
                if (response.status == 200) {
                    this.$toast.success("Cập nhật dịch vụ thành công");
                    this.$emit("updateProductSuccess", product)
                }
            });
        },
        show(item) {
            this.$emit("updateProduct", item);
        },
        deleteProduct(product, index) {
            this.$emit('deleteProduct', product, index);
        }
    }

}
</script>
